@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic&display=swap);
@charset "UTF-8";
* {
  box-sizing: border-box; }

html,
body,
#root {
  height: 100%;
  background-color: rgba(250, 250, 250, 0.5);
  min-width: 1248px; }

nav {
  min-width: 180px; }

.cursor-pointer {
  cursor: pointer; }

.main-wrapper {
  display: flex;
  flex-wrap: wrap;
  height: calc(100% - 65px); }

.content-head h3 {
  color: #212529;
  font-size: 32px;
  font-weight: 500; }

/* 주문관리 */
.order-circle {
  border-radius: 100%;
  width: 130px;
  height: 130px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-weight: 500;
  text-decoration: none;
  color: #000000;
  cursor: pointer; }

.today-board-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 30px; }

.today-board-list {
  max-width: 1024px; }

.order-list__section,
.today-board__section {
  max-width: 1024px; }

header {
  height: 65px; }

header.bg-dark.text-light.flex-row {
  padding: 10px 15px; }

header .menu-icon {
  font-size: 37px;
  margin-right: 17px; }

header .title {
  margin-bottom: 0;
  font-size: 37px; }

.signin {
  width: 500px;
  background-color: white;
  border: 1px solid #dee2e6;
  margin: 0 auto;
  margin-top: 100px;
  padding: 50px; }
  .signin .signin__title {
    text-align: center;
    margin-bottom: 40px;
    font-size: 2rem; }
  .signin .inner .input-group {
    margin-bottom: 10px; }
  .signin .inner .input-group .form-control {
    border-radius: 0; }
  .signin .inner .btn-submit {
    width: 100%; }

#menus table {
  text-align: center; }

.modal-footer {
  pointer-events: auto; }

#menuModal {
  background: #f8fbff;
  padding: 21px;
  border-radius: 11px; }
  #menuModal form[name="menuForm"] {
    width: 100%; }
  #menuModal .row {
    margin: 0; }
  #menuModal .input-group * {
    border-radius: 0;
    border-color: #6c757d; }
  #menuModal .input-group {
    margin-bottom: 2px; }
  #menuModal > .input-group:last-child {
    margin-bottom: 0; }
  #menuModal .content-inner-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: none; }
  @media (max-width: 992px) {
    #menuModal .content-inner-wrap {
      flex-direction: column-reverse; }
    #menuModal .left-wrap {
      width: 230px;
      margin: 0 auto;
      margin-bottom: 10px; }
    #menuModal .content-inner-wrap .input-group-wrap {
      width: 100%; }
    #menuModal .period .form-control {
      font-size: 14px; } }
  #menuModal .input-group-prepend {
    width: 9%;
    min-width: 87px; }
  #menuModal .input-group-prepend .input-group-text {
    width: 100%;
    /* text-align: center; */
    display: flex;
    justify-content: center; }
  #menuModal .menu-image {
    width: 230px;
    border: 1px solid #6c757d;
    box-sizing: border-box; }
  #menuModal .input-group-wrap {
    width: 478px; }
  #menuModal img {
    width: 100%; }
  #menuModal .half {
    width: 50%; }
  #menuModal .category .input-group-append .dropdown-toggle {
    display: block;
    width: 100%;
    background: white; }
  #menuModal .category .input-group-append {
    width: calc(100% - 87px);
    margin-left: 0; }
  #menuModal .category .btn-outline-secondary:hover {
    color: #6c757d; }
  #menuModal label.choice {
    margin-right: 0;
    margin-bottom: 0; }
  #menuModal .form-control {
    display: flex;
    align-items: center;
    /* justify-content: space-between; */ }
  #menuModal .monthly .input-group {
    width: 68px;
    /* display: flex; */
    margin-right: 17px;
    line-height: 1.15; }
  #menuModal .period .form-control {
    justify-content: space-around; }
    #menuModal .period .form-control input {
      text-align: center;
      font-weight: 300;
      border: 1px solid rgba(0, 0, 0, 0.2);
      border-radius: 0.3rem;
      outline: 0; }
  #menuModal .monthly .input-group input[type="radio"] {
    margin-right: 9px; }
  #menuModal .options .form-control {
    font-size: 14px;
    height: 38px; }
  #menuModal .left-wrap .reg-image__button {
    width: 100%;
    margin-top: 5px; }
  #menuModal .modal-footer {
    border-top: 0;
    padding-bottom: 0;
    padding-right: 0;
    padding-left: 0; }
  #menuModal .modal-footer .btn {
    width: 105px; }

#menus .preview {
  display: flex;
  justify-content: center;
  align-items: center; }
  #menus .preview img {
    max-width: 52px; }

#menus .table th,
#menus .table td {
  vertical-align: middle; }

#menus tbody tr > :not(.check) {
  cursor: pointer; }

#menus tbody tr:hover {
  background: #dee2e6 !important; }

.order-id {
  color: #007bff;
  text-decoration: underline;
  cursor: pointer; }

.today-order {
  width: 1024px; }
  .today-order h1 {
    font-size: 30px; }
  .today-order .today-order-circles {
    display: flex;
    justify-content: space-between; }

.today-order-list {
  width: 1024px; }
  .today-order-list h1 {
    font-size: 30px; }

.order {
  width: 1024px; }
  .order h1 {
    font-size: 30px; }

.statistics {
  width: 1024px; }
  .statistics .menu-chart {
    position: relative;
    display: flex;
    width: 100%;
    height: 400px;
    margin-bottom: 20px; }
    .statistics .menu-chart .chart-form-container {
      flex: 1 1; }
      .statistics .menu-chart .chart-form-container h1 {
        font-size: 30px; }
    .statistics .menu-chart .chart-wrapper {
      flex: 2.5 1; }
      .statistics .menu-chart .chart-wrapper .chartjs-render-monitor {
        border: 1px solid #adb5bd;
        padding: 15px; }
  .statistics .age-chart {
    position: relative;
    display: flex;
    width: 100%;
    height: 400px; }
    .statistics .age-chart .chart-form-container {
      flex: 1 1; }
      .statistics .age-chart .chart-form-container h1 {
        font-size: 30px; }
    .statistics .age-chart .chart-wrapper {
      flex: 2.5 1; }
      .statistics .age-chart .chart-wrapper .chartjs-render-monitor {
        border: 1px solid #adb5bd;
        padding: 15px; }

